html, body {
	font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	display: flex;
	justify-content: center;
	align-content: center;
}

h1 {
	text-align: center;
}

.githubLink {
	text-align: center;
}

.gamepadState {
	background-color: rgba(116, 199, 232, 0.55);

	margin-top: 10px;
	margin-bottom: 10px;
	padding: 10px;
}

.addRemoveTouch {
	background-color: rgba(116, 124, 232, 0.55);

	margin-top: 10px;
	margin-bottom: 10px;
	padding: 10px;
}

.addRemoveTouch button {
	background-color: aliceblue;
}

.preventDefault {
	height: 500px;
	background-color: rgba(199,116,232, 0.55);

	margin-top: 10px;
	margin-bottom: 10px;
	padding: 10px;
}

.gamepad {
	position: fixed;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: flex-end;
	width: 100%;
	height: 100%;
	opacity: 0.5;
	pointer-events: none;
}

.gamepad svg {
	width: 50%;
	height: 20%;
	pointer-events: auto;
}



